const theme = {
  LIGHT: 'light-theme',
  DARK: 'dark-theme',
};
const bodyRef = document.querySelector('body');
const inputRef = document.getElementById('theme-switch-toggle');

inputRef.addEventListener('click', function handlerCheckbox() {
  // console.log('parsedValue.DARK:', parsedValue.DARK);

  const themeString = localStorage.setItem('theme', JSON.stringify(theme));
  // console.log(themeString);
  const value = localStorage.getItem('theme'); //string
  console.log(value);
  const parsedValue = JSON.parse(value);
  console.log(parsedValue);

  // if (parsedValue.DARK) {
  //   console.log('removing dark theme');
  //   bodyRef.classList.remove('dark-theme');
  //   localStorage.removeItem('theme');
  // }

  if (parsedValue.DARK) {
    console.log('adding dark theme');
    bodyRef.classList.add('dark-theme');
  }
});

